<script>
import config from "@/common/config";
import utils from "@/common/utils";
import service from "@/common/service";
import moment from "moment";
export default {
  // el: "#app",
  data() {
    return utils.createModel({
      fileName: "",
      invalidName: false,
      defaultField: {
        name: "",
        type: "",
        options: ""
      },
      defaultForm: {
        _id: "",
        name: "",
        createdBy: "",
        createdAt: "",
        updatedBy: "",
        updatedAt: "",
        type: "form",
        formData: []
      },
      auto: false,
      utils: utils,
      form: {},
      field: {},
      moment: moment,
      config: config
    });
  },
  created: function() {
    this.reset();
  },
  methods: {
    reset: function() {
      this.form = JSON.parse(JSON.stringify(this.defaultForm));
      this.field = JSON.parse(JSON.stringify(this.defaultField));
      this.auto = false;
      this.field = {};
      this.fileName = "";
      this.invalidName = false;
      $("#formUpload").val("");
    },
    upload(oEvent) {
      var rABS = true;
      var that = this;
      var files = oEvent.target.files;
      var f = files[0];
      this.fileName = f.name;
      var reader = new FileReader();
      var auto = this.auto;
      reader.onload = function(e) {
        var data = e.target.result;

        if (!rABS) data = new Uint8Array(data);
        var workbook = XLSX.read(data, { type: rABS ? "binary" : "array" });
        var ws = workbook.Sheets[workbook.SheetNames[0]];
        var aoa = XLSX.utils.sheet_to_json(ws, { header: 1 });

        var fields = utils.buildFormData(aoa, auto);
        that.form.formData = fields;
       
        $("#formUpload").val("");
      };
      if (rABS) reader.readAsBinaryString(f);
      else reader.readAsArrayBuffer(f);
    },
    selectField(field) {
      var index = this.form.formData.indexOf(field);
      var newField = { index: index };
      $.extend(newField, field);
      this.field = newField;
    },
    removeField(field) {
      var index = this.form.formData.indexOf(field);
      this.form.formData.splice(index, 1);
    },
    saveField() {
      var index = this.field.index;
      delete this.field.index;
      var newField = $.extend({}, this.field);
      this.form.formData[index] = newField;
      this.field = JSON.parse(JSON.stringify(this.defaultField));
      $(".default-example-modal-right-sm").modal("hide");
    },
    saveForm() {
      var that = this;
      this.invalidName = false;
      if (!this.form.name) {
        this.invalidName = true;
        return;
      }
      var form = JSON.parse(JSON.stringify(this.form));
      form.formData = utils.toFormIO(form.formData);
      form.createdBy = this.user.id;
      form.createdAt = moment().format("x");
      form._id = moment().format("x");
      form.key = "";
      service.saveItem("form",form).then(
        result => {
          this.reset();
          this.$router.push({
            name: "forms",
            params: {}
          });
        },
       result=>{
          this.invalidName = true;
           this.$parent.$emit("message", {
              type: "error",
              title: "",
              message:result.message
            });
         }
      );
    }
  }
};
</script>

